@mixin mobile{
  @media screen and (max-width: 768px) {
  .home .container .sidebar {
    display: none; /* Hide the sidebar in mobile and tablet views */
  }

  .home .container .chat {
    flex: 1; /* Expand the chat area to fill the space */
  }
}
}

@mixin tablet{
  @media screen and (max-width: 768px) {
    .home .container .sidebar {
      display: none; /* Hide the sidebar in mobile and tablet views */
    }
  
    .home .container .chat {
      flex: 1; /* Expand the chat area to fill the space */
    }
  }
}

.formContainer{
  background-color: #a7bcff;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper{      
    background-color: white;
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;      

    .logo{
      color: #5d5b8d;
      font-weight: bold;
      font-size: 25px;
    }

    .title{
      color: #5d5b8d;
      font-size: 12px;
    }

    form{
      display: flex;
      flex-direction: column;
      gap: 15px;
       
      input{
        padding: 10px;
        border: none;
        width: 250px;
        border-bottom: 1px solid #cfdaff;
        &::placeholder{
          color: rgb(175, 175, 175);
      }
      }

      button{
        background-color: #5d5b8d;
        color: aliceblue;
        padding: 10px;
        border: none;
        font-weight: bold;
        cursor: pointer;
      }

      label{
        display: flex;
        align-items: center;
        gap: 10px;
        color: #5d5b8d;
        font-size: 12px;
        cursor: pointer;

        img{
          width: 32px;
          color: #5d5b8d;
          filter: grayscale(0%) brightness(0.5) sepia(100%) hue-rotate(220deg) saturate(300%);
          -webkit-filter: grayscale(0%) brightness(0.5) sepia(100%) hue-rotate(220deg) saturate(300%);
        }
      }
    }

    p{
      color: #5d5b8d;
      font-size: 12px;
      margin-top: 10px;
    }
  }
}

.home{
  background-color: #a7bcff;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  .container {
    border-radius: 10px;
    border: 1px solid white;
    width: 100%;
    height: 100%;
    display: flex;
    // overflow: hidden;
    // @include mobile{
    //   width: 90%;
    // }
    // @include tablet{
    //   width: 90%;
    // }
    
    .sidebar{
      flex: 0.5;
      border-right: 1px solid #3e3c61;
      background-color: #3e3c61;
      position: relative;
      .navbar{
        display: flex;
        align-items: center;
        background-color: #191740;
        height: 50px;
        padding: 10px;
        justify-content: space-between;
        color: #ddddf7;
        .logo {
          font-weight: bold;
          // @include mobile{
          //   display: none;
          // }
          // @include tablet{
          //   display: none;
          // }
        }
        .user{
          display: flex;
          gap: 10px;
          img{
            background-color: #ddddf7;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            object-fit: cover;
            cursor: pointer;
          }
          button {
            background-color: #5d5b8d;
            color: #ddddf7;
            font-size: 10px;
            border: none;
            cursor: pointer;
            // @include tablet{
            //   position: absolute;
            //   bottom: 10px;
            }
          }
        }
      }
      .search{
        border-bottom: 1px solid gray;
        .searchForm{
          padding: 10px;
          input{
            background-color:transparent;
            border: none;
            color: white;
            outline: none;
            &::placeholder{
              color: lightgrey;
            }
          }
        }
      }

      .userChat{
        padding: 8px;
        display: flex;
        align-items: flex-start;
        gap: 8px;
        color: antiquewhite;
        cursor: pointer;
        &:hover {
          background-color: rgb(27, 27, 88);
        }

        img{
          width: 50px;
          height: 50px;
          border-radius: 50%;
          object-fit: cover;
        }

        .userChatInfo {
          display: flex;
          flex-direction: column;
          span{
            font-size: 18px;
            font-weight: 500;
            margin-top: 2px;
          }
          p{
            font-size: 14px;
            color: lightgray;
            margin-top: 3px; 
            margin-bottom: 1px;
          }
        }
      }
    }
    .chat {
      flex: 2;
     .chatInfo{
       height: 60px;
       background-color: #5d5b8d;
       display: flex;
       align-items: center;
       justify-content: space-between;
       color: lightgray;
      } 

      .chatIcons{
        display: flex;
        gap: 10px;

        img{
          height: 24px;
          cursor: pointer;
          margin-right: 8px;
        }
      }

      .messages{
        background-color: #ddddf7;
        padding: 10px;
        height: calc(100% - 140px);
        transform-origin: top center;
        overflow-y: scroll;
        
        .message{
          display: flex;
          gap: 20px;

          .messageInfo{
            display: flex;
            flex-direction: column;
            color: gray;
            font-weight: 300;
            margin-bottom: 20px;

            img{
              width: 40px;
              height: 40px;
              border-radius: 50px;
              object-fit: cover;
            }

          }
          .messageContent{
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;
            p{
              background-color: white;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              max-width:max-content;
            }
            img{
              width: 50%;
            }
          }
          &.owner{
            flex-direction: row-reverse;
            .messageContent{
            align-items: flex-end;
            p{
              background-color: #a7bcff;
              color: white;
              border-radius: 10px 0px 10px 10px;
              } 
            }
          }
        }
      }

      .input{
        height: 40px;
        background-color: white;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        input{
          width: 100%;
          border: none;
          outline: none;
          color: #2f2d52;
          font-size: 18px;

          &::placeholder{
            color: lightgray;
          }
        }
        .send{
          display: flex;
          align-items: center;
          gap: 10px;
          img{
            height: 24px;
            cursor: pointer;
          }
          button{
            border: none;
            padding: 10px 15px;
            color: white;
            background-color: #5d5b8d;
            cursor: pointer;
            outline: none;
          }
          .file-label {
            display: flex; /* Horizontally align images */
            align-items: center; /* Vertically align images */
            gap: 5px; /* Space between images */
          }
        }
      }
    }      
  }

     
      